import React, { useState, useRef, forwardRef, Fragment } from 'react';
import FormContext from 'react-bootstrap/FormContext'

import { useForm, FormProvider, useFormContext } from "react-hook-form";
import {  Form, Button ,Col} from 'react-bootstrap';

import {useNavHeight} from './Context.js'
import { Step, StepGroup } from "./Step.js";
import Swal from 'sweetalert2'

const useScroll = () => {
    const htmlElRef = useRef(null)
    const executeScroll = (minusOffset=0) => window.scrollTo(0, htmlElRef.current.offsetTop - minusOffset)
    return [executeScroll, htmlElRef]
}


const Radio = forwardRef((props, ref) => {
    const { errors } = useFormContext();
    return (<div>
        <Form.Group as={Col}>
            <Form.Label>{props.title}<ErrorMsg error={errors[props.name]} /></Form.Label>
            {props.option.map((x, index) => {
                return <div>
                    <Form.Check
                        ref={ref}
                        name={props.name}
                        type="radio"
                        id={`${props.name}${index}`}
                        label={x}
                        value={x}
                    />
                </div>
            })}

        </Form.Group>
    </div>)
})

function formatKey(obj) {
    const newObj = {}
    Object.entries(obj).map((x) => newObj["entry." + x[0]] = x[1])
    return newObj
}

//this method only for flatten obj
function toUrlParam(obj) {
    // https://stackoverflow.com/questions/14525178/is-there-any-native-function-to-convert-json-to-url-parameters
    return new URLSearchParams(obj).toString();
}


const NumSpinner = forwardRef((props, ref) => {

    const [value, setValue] = useState(0)
    function add(num) {
        setValue(value + num < 0 ? 0 : value + num)
    }

    return (<div className="input-group mb-3">
        <div className="input-group-prepend">
            <button className="btn btn-sm" style={{ backgroundColor: "#ffa9a9" }} onClick={(e) => { e.preventDefault(); add(-1); return false }}><i style={{ color: "white" }} className="fa fa-minus"></i></button>
        </div>
        <input ref={ref} type="number" name={props.name} className="form-control form-control-sm" value={value} min="0" readOnly />
        <div className="input-group-prepend">
            <button className="btn btn-sm" style={{ backgroundColor: "#ffa9a9" }} onClick={(e) => { e.preventDefault(); add(1); return false }}><i style={{ color: "white" }} className="fa fa-plus"></i></button>
        </div>
    </div>
    )
})

const NameControl = forwardRef((props, ref) => {
    const context = React.useContext(FormContext)
    return (<Fragment>
        <Form.Control name={context.controlId} ref={ref} {...props} />
    </Fragment>)
})


Object.byString = function (o, s) {
    s = s.replace(/\[(\w+)\]/g, '.$1'); // convert indexes to properties
    s = s.replace(/^\./, '');           // strip a leading dot
    var a = s.split('.');
    for (var i = 0, n = a.length; i < n; ++i) {
        var k = a[i];
        if (k in o) {
            o = o[k];
        } else {
            return;
        }
    }
    return o;
}

const ErrorMsg = (props) => { return <Fragment>{props.error && <p style={{ color: '#002bff', display: 'inline' }}>({props.error.message || "Required"})</p>}</Fragment> }

function CustomGoogleForm() {

    const methods = useForm({ mode: "onBlur" });
    const { register, trigger, reset, getValues  ,errors, formState } = methods;
    const navHeight = useNavHeight()
    
    const [executeScroll, elementToScrollRef] = useScroll()

    const FORM_URL = "https://docs.google.com/forms/u/0/d/e/1FAIpQLSfS1K4cvOkUaQOr7SBKwu3cmL5G5j3t9NebJaANzsCqsqPAqQ/formResponse";


    // simple to get json
    // FB_PUBLIC_LOAD_DATA_[1][1].map((x)=>{ return {a:x[1],b:x[4]}}).filter(x=>x.b).map(x=>{return {a:x.a,b:x.b[0][0]}})
    // 0: {a: "姓名", b: 1097284387}
    // 1: {a: "是否願意與我們共進午宴？", b: 2116115982}
    // 2: {a: "是否參加午宴前的證婚儀式？(地點同婚宴場地)", b: 1596335383}
    // 3: {a: "您的聯絡電話是？", b: 943713509}
    // 4: {a: "是否需要寄送喜帖？", b: 788279090}
    // 5: {a: "請給我們您的喜帖寄送地址", b: 953458254}
    // 6: {a: "我認識帥氣的 或是美麗的", b: 246555755}
    // 7: {a: "您和新郎、新娘的關係是？", b: 1865097167}
    // 8: {a: "當天出席人數", b: 2044196324}
    // 9: {a: "是否需要兒童座椅與餐具", b: 1492883860}
    // 10: {a: "是否需要素食餐點", b: 749493017}
    // 11: {a: "我想說 . . .", b: 1693858308}

     window.Swal=Swal
    const submit = data => {
        const payload = Object.assign({}, { "pageHistory": [0, 1, 2] }, formatKey(data))
        const requestOptions = {
            method: 'POST',
            headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
            body: toUrlParam(payload)
        };

        fetch("https://cors-anywhere.herokuapp.com/" + FORM_URL, requestOptions)
          .then(response => {
            if (response.ok) {
              Swal.fire(
                '2020/10/10 喬迪 & 奕潔 ',
                '一起來分享我們的喜悅吧！ 等你唷 ～',
                'success'
              )
              ref.current.getControl().to(1)
              reset({})
              return
            }
            throw response
          }
          )
          .catch((err) => {
            Swal.fire({
              icon: 'error',
              title: 'Oops...',
              text: '表單出現了一些問題 請晚點填寫 麻煩您。',
            })
          })
    }
    const ref = useRef()
    // window.errors = errors
    window.formState = formState

    const validate = (names, callback) => {
        return async () => await trigger(names) ? callback() : null
    }
    const step1Valids = ["1097284387", "1596335383", "2116115982", "943713509", "788279090"]
    const step2Valids = ["246555755", "1865097167"]

    
    //entry.1235456 will block the browser .
    //see https://github.com/react-hook-form/react-hook-form/issues/2079
    return (<div ref={elementToScrollRef} >

        <FormProvider {...methods} >
            <Form url={FORM_URL} onSubmit={(e)=>{e.preventDefault();submit(getValues());executeScroll(navHeight)}}>
                <div style={{ margin: "15px", textAlign: "left" }}>

                    <StepGroup ref={ref}>
                        <Step key="step1" name="logins-part" header="第一" svg={<img alt="bride" style={{ width: "100%" }} src={"/image/bride.svg"} />}>

                            <Form.Group as={Col} controlId="1097284387">
                                <Form.Label>姓名 <ErrorMsg error={errors["1097284387"]} /></Form.Label>
                                <NameControl ref={register({ required: true })} test="5" onChange={(x) => { }} placeholder="Name " />
                                <small className="form-text text-muted">請填寫姓名 ，如: 沈佳宜   </small>
                            </Form.Group>

                            <Radio ref={register({ required: true })} name="2116115982" title="是否願意與我們共進午宴？"
                                option={["一定要參加，拜託算我一份！", "禮到人不到，請還是寄喜帖給我噢！", "可惜無法出席，祝福的心與新人同在！"]} />
                            <Radio ref={register({ required: true })} name="1596335383" title="是否參加午宴前的證婚儀式？(同婚宴場地-池畔教堂)"
                                option={["我要參加！到場見證幸福時刻！", "無法出席，獻上最誠摯的祝福！"]} />

                            <Form.Group as={Col} controlId="943713509">
                                <Form.Label>電話<ErrorMsg error={errors["943713509"]} /></Form.Label>
                                <NameControl ref={register({
                                    required: true,
                                    pattern: {
                                        value: /^0[0-9]{9}$/,
                                        message: "電話號碼不正確"
                                    }
                                })} placeholder="phone number" />

                            </Form.Group>

                            <Radio ref={register({ required: true })} name="788279090" title="是否需要寄送喜帖？" option={["我想收到紙本喜帖，請寄給我", "婚禮相關資訊我都知道了，不用特別寄喜帖給我囉"]} />

                            <Form.Group as={Col} controlId="953458254">
                                <Form.Label>請給我們您的喜帖寄送地址</Form.Label>
                                <small className="form-text text-muted">只有需要收到紙本喜帖的人才需填寫，郵遞區號不要忘記囉，謝謝！</small>
                                <NameControl name="953458254" ref={register} placeholder="address" />
                            </Form.Group>

                            <div style={{ textAlign: "center" }}>
                                <Button onClick={validate(step1Valids, () => { ref.current.getControl().next(); executeScroll(navHeight) })}  >下一步</Button>
                            </div>
                        </Step>




                        <Step key="step2" name="information-part" header="第二" svg={<img alt="ring" style={{ width: "100%" }} src={"/image/ring.svg"} />}>

                            <Radio ref={register({ required: true })} name="246555755" title="我認識帥氣的 或是美麗的 " option={["新郎", "新娘"]} />

                            <Form.Group as={Col} controlId="1865097167">
                                <Form.Label>您和新郎、新娘的關係是？<ErrorMsg error={errors["1865097167"]} /></Form.Label>
                                <NameControl style={{ color: "#ffa9a9" }} ref={register({ required: true })} as="select" custom>
                                    <option></option>
                                    <option>國中同學</option>
                                    <option>高中同學</option>
                                    <option>大學同學</option>
                                    <option>親友</option>
                                    <option>同事</option>
                                    <option>其他朋友</option>
                                </NameControl>
                            </Form.Group>

                            <Form.Group as={Col} controlId="2044196324">
                                <Form.Label>當天出席人數</Form.Label>
                                <small className="form-text text-muted">請填入包含自己在內的人數 ( 不包含兒童椅人數，如需兒童座椅請在下題填寫 )</small>
                                <NumSpinner ref={register} name="2044196324" />

                            </Form.Group>

                            <Form.Group as={Col} controlId="1492883860">
                                <Form.Label>是否需要兒童座椅與餐具</Form.Label>
                                <small className="form-text text-muted">需要的朋友請填寫數量，單身者請跳過</small>
                                <NumSpinner ref={register} name="1492883860" />
                            </Form.Group>

                            <Form.Group as={Col} controlId="749493017">
                                <Form.Label>是否需要素食餐點</Form.Label>

                                <small className="form-text text-muted">需要的朋友請填寫素食餐點數量，吃肉的朋友請跳過</small>
                                <NumSpinner ref={register} name="749493017" />
                            </Form.Group>

                            <div style={{ textAlign: "center" }}>
                                <Button onClick={validate(step2Valids, () => { ref.current.getControl().next(); executeScroll( window.innerHeight*23/100+ navHeight) })}  >下一步</Button>
                            </div>
                        </Step>





                        <Step key="step3" name="information-part3" header="第三" svg={<img alt="groom"  style={{ width: "100%" }} src={"/image/groom.svg"} />}>
                            <div>
                                <p style={{ textAlign: "center", color: "#ffa9a9" }}>很謝謝您的填寫，最後有什麼話想趁機對我們告白的都可以說哦!!!</p>
                                <Form.Group as={Col} controlId="1693858308">
                                    <Form.Label>我想說 . . .</Form.Label>
                                    <NameControl ref={register} placeholder="I wanna say ~" />
                                </Form.Group>
                            </div>


                            <div style={{ textAlign: "center" }}>
                                <Button variant="primary" type="submit"> 送出</Button>
                            </div>
                        </Step>

                    </StepGroup>
                </div>

                {/* //for test purpose
                <Button onClick={() => ref.current.getControl().previous()} >上一個</Button>
                <Button onClick={() => ref.current.getControl().next()} >下一個</Button>
                <Button variant="primary" type="submit"> Submit</Button> */}

            </Form>
        </FormProvider>

    </div>)
}

export default CustomGoogleForm