function useDomSize(ref) {
  const [size, setSize] = useState([0, 0]);
  useLayoutEffect(() => {
    const elem = ref.current
    function updateSize() {
      setSize([elem.offsetWidth, elem.offsetHeight]);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, [ref]);
  return size;
}

const Link = (props) => {
  const navHeight =useNavHeight()
  const minus = props.minus||0
  const fromTop= minus+navHeight
  return (<a id={props.id} style={{top:`-${fromTop}px`,"visibility":"visible"}} className="link"></a>)
}

const ParallaxImg= (props)=>{
  const matches = useRWD()
  return(<Fragment>
          {matches.small?<div class="parallax" style={{position:"relative",height:"auto" ,backgroundImage: `url(${props.src})`}}>
            <img alt={props.src} src={props.src} style={{"width":"100%"}}/>
            {props.children}
          </div> :
        <div className="parallax" style={{ position:"relative",backgroundImage: `url(${props.src})`}}>{props.children}</div>}
  </Fragment>)
}

