import React, { useState, useEffect } from 'react';
import { Card } from 'react-bootstrap';
import axios from 'axios';
import Masonry from 'react-masonry-css'

function Bless() {

    const url = "https://script.google.com/macros/s/AKfycbyFyQ6OrCWSRDdE28OfXwhHsExDhPNDdx6qHCsD3GlyE8RjedEL/exec"
  
    // const Colors = ["#79e1d1", "#ffc0c0", "#9fd9ff", "#ffc0f6"]
    // const test = [{ "name": "黃__", "time": "2020-06-08T10:46:57.130Z", "msg": "欠揍啊!!" }, { "name": "石_", "time": "2020-06-10T09:39:34.320Z", "msg": "10月10號見阿" }, { "name": "G_", "time": "2020-06-12T09:51:34.205Z", "msg": "恭喜你們" }, { "name": "余__", "time": "2020-06-12T14:58:26.484Z", "msg": "I love you" }, { "name": "黃__", "time": "", "msg": "祝你們幸福" }, { "name": "你__", "time": "", "msg": "這是測試" }, { "name": "你______", "time": "", "msg": "哇哇哇" }]
  
  
    // const [data, setData] = useState(test);
    const [data, setData] = useState([]);
  
    async function initData() {
      const result = await axios.get(url);
      setData(result.data);
    }
    useEffect(() => {
      initData()
    }, []);
  
  
    const breakpointColumnsObj = {
      default: 4,
      1100: 3,
      700: 2,
      500: 1
    };
  
    return (<div>
      <Masonry
        breakpointCols={breakpointColumnsObj}
        className="my-masonry-grid"
        columnClassName="my-masonry-grid_column"
      >
        {data.map((x, index) => {
          return <Card key={"card"+index} style={{ backgroundColor: "#ffa9a9", borderRadius: "15px", color: "white" }} className="text-center">
            <Card.Body>
              <Card.Title>{x.name}</Card.Title>
              <Card.Text>
                {x.msg}
              </Card.Text>
            </Card.Body>
          </Card>
        })}
      </Masonry>
    </div>
    )
  }


  export default Bless