import React, { memo } from 'react';
import ImageGallery from 'react-image-gallery';
import "./image-gallery.scss";
import { createGlobalStyle } from "styled-components";
import useRWD from './RWD';
import {useNavHeight} from './Context.js'

const CustomImageRender = memo((props) => {
  const small = Object.assign({}, props.item.cssSmall)
  const notSmall = Object.assign({ "objectFit": "contain" }, props.item.css)
  const matches = useRWD()
  return <div>
    <img alt={props.item.original} className='image-gallery-image' style={matches.small ? small : notSmall} src={props.item.original} />
  </div>
}, () => true)

const root_url = process.env.PUBLIC_URL;
const images = [
  {
    original: root_url + '/image/01.2000.1200.jpg',
    thumbnail: root_url + '/image/01.250.150.jpg',
    cssSmall: {},
    css: { "objectFit": "contain" },
  }, {
    original: root_url + '/image/02.2000.1200.jpg',
    thumbnail: root_url + '/image/02.250.150.jpg',
    cssSmall: { "object-position": "60%" },
    css: { "objectFit": "contain" }
  }, {
    original: root_url + '/image/03.jpg',
    cssSmall: { "object-position": "65%" },
  }, {
    original: root_url + '/image/04.jpg',
  }, {
    original: root_url + '/image/05.jpg',
  }, {
    original: root_url + '/image/06.jpg',
    cssSmall: { "object-position": "75%" },
  }, {
    original: root_url + '/image/07.jpg',
    cssSmall: { "object-position": "35%" },
  },

];



function MyGallery() {

  const navHeight = useNavHeight()
    
  const styleHeight = window.innerHeight-navHeight
  const MyFonts = createGlobalStyle` 
  .image-gallery-content .image-gallery-slide .image-gallery-image{
    height:${styleHeight}px
  }
  `
  const options = {
    infinite: true,
    showThumbnails: false,
    showBullets: true,
    autoPlay: true,
    swipingTransitionDuration: 2000,
    slideDuration: 500,
  }
  return (<div style={{ backgroundColor: "black" }}>
    <MyFonts/>
    <ImageGallery {...options} useBrowserFullscreen={true} lazyLoad={false} items={images} renderItem={(item) => <CustomImageRender item={item} />} />
  </div>)
}

export default MyGallery