import React, { useEffect, useRef, forwardRef, useImperativeHandle } from 'react';
import Stepper from 'bs-stepper'
import 'bs-stepper/dist/css/bs-stepper.min.css';

const StepGroup = forwardRef((props, ref) => {

    const stepperRef = useRef()
    const render = props.children.map(x => (0, x.type)(x.props))
    const stepper = useRef()
  
    useImperativeHandle(ref, () => {
      return {
        "getControl": (() => stepper.current)
      }
    })
  
    useEffect(() => {
      stepper.current = new Stepper(stepperRef.current)
    }, [])
  
    return (<div ref={stepperRef} className="bs-stepper">
      <div className="bs-stepper-header">
        {render.map(x => x.header)
          .flatMap((x ,i)=> [x, (<div key={"bs-stepper-header"+i} className="line"></div>)])
          .slice(0, render.length * 2 - 1)}
      </div>
      <div className="bs-stepper-content">
        {props.children.map(x => (0, x.type)(x.props).content)}
      </div>
    </div>
    )
  })
  
  function StepHeader(props) {
  
    return (<div className="step" data-target={"." + props.name}>
      <button type="button" className="step-trigger"  >
        <span className="bs-stepper-circle">
            {props.svg||1}
        </span>
        <span className="bs-stepper-label">{props.header}</span>
      </button>
    </div>)
  }
  
  function StepContent(props) {

    return (<div>
      <div className={"content " + props.name} >
        {props.children}
      </div>
    </div>)
  }
  
  /**
   * This will make debug in react extension a little difficult
   */
  function Step(props) {
    const name = props.name
    const content = <StepContent name={name}>{props.children}</StepContent>
    const header = <StepHeader {...props} />
    return { header: header, content: content }
  }

  export{
    StepGroup,
    Step,
  }